.content {
  padding: 1em 20vw;
  max-height: calc(100vh - 2em - 64px);
  overflow: auto;
  position: relative;
}

.content-wrapper {
  padding: 1em;
}

@media (max-width: 1000px) {
  .content {
    padding: 1em 5vw;
  }
}



